.cookie-consent-banner {
  background: var(--background-alt, #1a1a2e);
  border-top: 1px solid var(--border, #333);
  z-index: 10000;
  padding: 1rem;
  transition: transform .3s ease-in-out;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateY(100%);
  box-shadow: 0 -4px 20px #0000004d;
}

.cookie-consent-banner.visible {
  transform: translateY(0);
}

.cookie-consent-content {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
}

.cookie-consent-content p {
  color: var(--text, #e0e0e0);
  margin: 0;
  font-size: .95rem;
  line-height: 1.5;
}

.cookie-consent-content a {
  color: var(--links, #6db3f2);
  text-decoration: underline;
}

.cookie-consent-content a:hover {
  color: var(--links-hover, #9ecbf7);
}

.cookie-consent-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: .75rem;
  display: flex;
}

.cookie-btn {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: .6rem 1.25rem;
  font-size: .9rem;
  font-weight: 500;
  transition: all .2s;
}

.cookie-btn-accept {
  background: var(--links, #6db3f2);
  color: #000;
}

.cookie-btn-accept:hover {
  background: var(--links-hover, #9ecbf7);
  transform: translateY(-1px);
}

.cookie-btn-decline {
  color: var(--text, #e0e0e0);
  border: 1px solid var(--border, #444);
  background: none;
}

.cookie-btn-decline:hover {
  border-color: var(--text, #e0e0e0);
  background: #ffffff1a;
}

@media (prefers-color-scheme: light) {
  .cookie-consent-banner {
    background: #f8f9fa;
    border-top-color: #dee2e6;
    box-shadow: 0 -4px 20px #0000001a;
  }

  .cookie-consent-content p {
    color: #212529;
  }

  .cookie-btn-accept {
    color: #fff;
    background: #0d6efd;
  }

  .cookie-btn-accept:hover {
    background: #0b5ed7;
  }

  .cookie-btn-decline {
    color: #212529;
    border-color: #adb5bd;
  }

  .cookie-btn-decline:hover {
    background: #0000000d;
  }
}

@media (width <= 480px) {
  .cookie-consent-banner {
    padding: .75rem;
  }

  .cookie-consent-content p {
    font-size: .85rem;
  }

  .cookie-btn {
    padding: .5rem 1rem;
    font-size: .85rem;
  }

  .cookie-consent-buttons {
    width: 100%;
  }

  .cookie-btn {
    flex: 1;
    min-width: 120px;
  }
}

